import './styles/index.css'
import App from './pages/app/app.vue'
import lbCom from './components'
import stores from './stores'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'

Vue.use(MintUI)
Vue.use(Vuex)
Vue.use(lbCom)


 let store = new Vuex.Store(stores)

new Vue({
    el: '#app',
    store,
    render: h => h(App)
})


